import React from 'react';
import { connect } from 'react-redux';

const shortName = name => name.split(' ')[0];

const picURL = (id) => `https://unsplash.it/300/300?image=${ id }`;

const Picture = ({ src, author }) =>
  <div>
    <h4>{ shortName(author) }</h4>
    <img src={ src } alt="" />
  </div>;

const PictureGallery = ({ picList = () => {}, showLimit = 10 }) =>
  <ul>
    { picList.map( (pic, i) =>
      <li key={ i } className="am-pull--left">
        <Picture src={ picURL(pic.id) } author={ pic.author} />
      </li>
    )}
  </ul>;

const Button = ({ onClick = () => {}, label, cssClasses = "" }) =>
  <button onClick={ onClick } className={ cssClasses }>{ label }</button>;

const DisplayData = ({ data, cssClasses = "" }) =>
  <div className={ cssClasses }>
    { data }
  </div>;

const Counter = ({ onDecrement = () => {}, onIncrement = () => {}, count = 0 }) =>
  <div className="row row--xs-middle">
    <div className="col--xs-4">
      <Button onClick={ onDecrement } label="-" cssClasses="am-button am-button--primary" />
    </div>
    <div className="col--xs-4 am-align--center">
      <DisplayData data={ count } cssClasses="" />
    </div>
    <div className="col--xs-4 am-align--right">
      <Button onClick={ onIncrement } label="+" cssClasses="am-button am-button--primary" />
    </div>
  </div>;

const App = ({ onDecrement = () => {}, onIncrement = () => {}, count = 0, pictures = {} }) =>
  <div className="app">
    <div className="container">
      <div className="row">
        <div className="col--xs-12">
          <Counter onDecrement={ onDecrement } onIncrement={ onIncrement } count={ count } />
        </div>
      </div>
      <div className="row">
        <div className="col--xs-12">
          <PictureGallery picList={ pictures.slice(0, count) } />
        </div>
      </div>
    </div>
  </div>;

const mapStateToProps = (state) => ({
  count: state.count,
  pictures: state.pictures,
});

const mapDispatchToProps = (dispatch) => ({
  onIncrement: () => dispatch({ type: 'INCREMENT', payload: { howMuch: 1 } }),
  onDecrement: () => dispatch({ type: 'DECREMENT', payload: { howMuch: 1 } })
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
